import { signOut } from 'firebase/auth';
import { NavLink } from 'react-router-dom';
import Grid from '@mui/material/Grid';
import CloseIcon from '@mui/icons-material/Close';
import IconButton from '@mui/material/IconButton';
import { Typography } from '@mui/material';
import Avatar from '@mui/material/Avatar';
import { Hearts } from 'react-loader-spinner';
import Link from '@mui/material/Link';
import Button from "@mui/material/Button";




const HeaderMenu = ({toggleModal , user, auth }) => {
    
    const outFromAccount = () => {
        signOut(auth).then(res => res)
        toggleModal()
    }

    if (user) {
        return (
            <Grid sx={{}} container justifyContent="center" alignItems="center" flexDirection="column" spacing={2}>
                   {/* <Grid item xs={4} md={4} display='flex' alignItems='center' flexDirection='row'>
                        <Typography variant='h3' sx={{ marginRight: '20px'}}>Чат</Typography>
                        <Hearts color="#ff6347" height={80} width={80} />
                    </Grid> */}
            <Grid item xs={12}  display='flex' alignItems='center' flexDirection='row'>
                <Avatar sx={{width: 80, height: 80, mr: 2}} src={`${user.photoURL}`} alt="avatar" />
                <Typography sx={{fontWeight: 500}} variant='h5'>
                    {user.displayName}
                </Typography>    
            </Grid>
            <Grid item xs={12} >
                <NavLink onClick={outFromAccount} className={'nav-link'} to={'/login'}>
                    <Button size='large' variant={'outlined'}>
                        Выйти с аккаунта
                    </Button>
                </NavLink>
            </Grid>            
            <Grid item xs={12} >
                {/*<Link variant='h5' href='/about'  underline='none'>*/}
                {/*    Про сайт*/}
                {/*</Link>*/}
                <NavLink onClick={toggleModal} className={'nav-link'} to={'/about'}>
                    <Typography variant='h5'>
                        Про сайт
                    </Typography>
                </NavLink>
            </Grid>
            <Grid item xs={12} >
                <NavLink onClick={toggleModal} className={'nav-link'} to={'/search'}>
                    <Typography variant={'h5'}>
                        Поиск
                    </Typography>
                </NavLink>
            </Grid>    
            <Grid item xs={12} >
                <IconButton onClick={toggleModal} >
                    <CloseIcon />
                </IconButton>
                
            </Grid>      
        </Grid >
        )
    }

    if (!user) {
    return (<Grid container justifyContent="center" alignItems="center" flexDirection="column" spacing={2}>
                   {/* <Grid item xs={4} md={4} display='flex' alignItems='center' flexDirection='row'>
                        <Typography variant='h3' sx={{ marginRight: '20px'}}>Чат</Typography>
                        <Hearts color="#ff6347" height={80} width={80} />
                    </Grid> */}
      
            <Grid item xs={12} >

                <NavLink className={'nav-link'} to={'/about'} onClick={toggleModal}>
                    <Typography variant={'h5'}>
                        Про сайт
                    </Typography>
                </NavLink>
            </Grid>
            <Grid item xs={12} >
                <NavLink className={'nav-link'} to={'/login'} onClick={toggleModal}>
                    <Typography variant={'h5'}>
                        Войти
                    </Typography>
                </NavLink>
            </Grid>    
            <Grid item xs={12} >
                <IconButton onClick={toggleModal} >
                    <CloseIcon />
                </IconButton>
            </Grid>      
        </Grid >
        
        );
        }
}

export default HeaderMenu;
